* {
    margin: 0;
    padding: 0;
    border: 0;
}

body {
    background-color: black;
    color: white;
}

.containerSubBecome {
    display: flex;
    flex-direction: row;
    gap: 10px;
    align-items: center;
}

.watchTrailer {
    color: rgb(194, 193, 193);
    font-family: "Proxima Nova A", sans-serif;
    font-size: 9px;
    letter-spacing: .1em;
    font-weight: 600;
    text-shadow: 3px 3px 8px rgba(0, 0, 0, 1);
    cursor: pointer;
    transition: color 0.5s ease-in-out;
}

.watchTrailer:hover {
    color: rgb(255, 255, 255);
}

.becomeBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid rgb(207,180,111);
    background-color: transparent;
    color: white;
    padding: 5px;
    font-family: "Proxima Nova A", sans-serif;
    letter-spacing: .1em;
    font-weight: 600;
    font-size: 8px;
    background-color: rgba(0, 0, 0, 0.514);
    transition-property: color, background-color;
    transition-duration: 0.35s;
    transition-timing-function: ease-in-out;
    width: 130px;
    font-size: 9px;
}

.becomeBtn:hover {
    cursor: pointer;
    color: black;
    background-color: rgb(207,180,111);
    background: linear-gradient(90deg, rgba(207,180,111,0.8267682072829132) 15%, rgba(232,222,195,1) 50%, rgba(203,175,102,1) 85%);
}



.smoke {
    z-index: -1;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    bottom: -120px;
    right: 2%;
    opacity: .5;
}

.divIntroImg {
    display: flex;
    justify-content: center;
    height: 400px;
    margin-top: -220px;
    position: relative;

}

.arkweek {
    height: 100px;
}

.assassinImg {
    height: 500px;
}


.logoText {
    height: 100px;
    filter: drop-shadow(0 0 20px rgb(0, 0, 0));
}

.divIntroImg {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 230px;
    max-width: 1920px;
    background-repeat: no-repeat;
    background-size: 100% 490px ;
    opacity: 0.9;
    margin-top: 0;
    margin-bottom: 100px;
}

.divApropos {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 430px;
    max-width: 1920px;
    position: relative;
    background-repeat: no-repeat;
    background-image: url(./bgApropos.jpeg); 
    background-size: 100% 490px ;
}

.line {
    background-image: linear-gradient( 90deg,rgba(191,162,81,0),#bfa251,rgba(191,162,81,0));
    width: 300px;
    height: 0.58px;
    display: flex;
    justify-content: center;
    align-items: center;
}


.lineSection {
    background-image: linear-gradient( 90deg,rgba(191,162,81,0),#c0b9a5,rgba(191,162,81,0));
    height: 0.58px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 99 !important
}


.lineSectionPlus {
    background-image: linear-gradient( 90deg,rgba(191,162,81,0),#b1aa98,rgba(191,162,81,0));
    height: 1px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 99 !important
}

.playText {
    text-shadow: 1px 6px 13px rgba(0,0,0,1);
    font-size: 17px;
    padding: 10px;
    font-family: times;
}

.odyssey {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: times;
    font-size: 30px;
    background-image: linear-gradient(90deg,transparent,rgba(0,0,0,.35) 10%,rgba(0,0,0,.35) 90%,transparent);
    height: 40px;
}


/* Arkweek section */
.divAssassinImg {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 300px;
    max-width: 1920px;
    position: relative;
    background-repeat: no-repeat;
    background-image: url(./assassin.jpg); 
    background-size: 100% 490px ;
}

.containerAllArkweek {
    display: flex;
    width: 700px;
    justify-content: center;
    align-items: center;
    height: 143px;
    background-image: linear-gradient(90deg,transparent,rgba(0, 0, 0, 0.767) 10%,rgba(0, 0, 0, 0.842) 90%,transparent);
    line-height: 2;
}

.titleArkweek {
 font-size: 11px;
}

.paraArkweek {
 font-size: 10px;
}

.containerArkweek {
    display: flex;
    flex-direction: column;
    font-family: "Proxima Nova A", sans-serif;
    color: rgb(201, 198, 198);
    padding: 20px;
}

/* news section */

.divNew {
   
    height: 500px;
}

.titleBoxNews {
   font-size: 8px;
   font-weight: 600;
   color: rgb(185, 183, 183);
   letter-spacing: .1em;
}

.containerDivNew {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 50px;
}


.containerBox {
    display: flex;
    flex-direction: row;
    gap: 20px;
    align-items: center;
    justify-content: center;
    font-family: "Proxima Nova A", sans-serif;
}

.boxNews {
    padding: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 190px;
    height: 500px;
    gap: 10px;
    overflow: hidden;
   
}

.lineSectionBox {
    background-image: linear-gradient( 90deg,rgba(191,162,81,0),#ffffff,rgba(191,162,81,0));
    height: 1px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 200px;
}

.subBox {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 200px;
    height: 300px;
    gap: 10px;
    background: rgba(0, 0, 0, 0.575);
    color: rgb(201, 200, 200);
}

.imgBoxNews {
    height: 110px;
    width: 100%;
    transition: transform .1s;

}

.imgBoxNews:hover {
    -ms-transform: scale(1.2); /* IE 9 */
    -webkit-transform: scale(1.2); /* Safari 3-8 */
    transform: scale(1.1);
    cursor: pointer;
}

.titleSubBoxNews {
    font-size: 12px;
}

.bigTitleNews {
    margin-top: -50px;
    text-shadow: 1px 1px 6px rgba(0, 0, 0, 1);
}

.readMore {
    margin-top: 50px;
    color: rgb(209, 209, 209);
    cursor: pointer;
    transition: color 0.5s ease-in-out;
}

.readMore:hover {
    color: white;
}

.paraBoxNews {
    font-size: 11px;
    height: 50px;
    width: 150px;
    text-align: center;
}

